import SignIn from "./signIn-model.js";
const signin = new SignIn()
export default {
	data() {
		return {
			userInfo: {},
			signInInfo: {
				list: [
					{str: false, signInBean: 0},
					{str: false, signInBean: 0},
					{str: false, signInBean: 0},
					{str: false, signInBean: 0},
					{str: false, signInBean: 0},
					{str: false, signInBean: 0},
					{str: false, signInBean: 0}
					],
				continuity: 0,
				today: 0
			}
		};
	},
	components: {

	},
	onLoad() {
		const that = this
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			// 签到信息查询
			that.getUserSignin(() => {
				callBack && callBack();
			})
		},
		signin() {
			const that = this
			if (that.signInInfo.today == 1) {
				signin.show_tips('今天已签到')
				return false
			}
			that.runSignin(() => {
				that.getUserSignin(() => {
					callBack && callBack();
				})
			})
		},
		// 签到
		runSignin(callBack) {
			const that = this
			signin.runSignin({}, (res) => {
				if (res.status_code == 'ok') {
					callBack && callBack();
				}
			})
		},
		// 签到信息查询
		getUserSignin(callBack) {
			const that = this
			signin.getUserSignin({}, (res) => {
				let data = res.data
				let cycleDays = data.continuity % 7
				let cycleList = []
				let signInBean = parseInt(data.startSignInBean)
				let increase = parseInt(data.increase)
				for (let i = 0; i < 7; i++) {
					let dayInfo = {}
					if (i >= cycleDays) {
						dayInfo['str'] = false
					} else {
						dayInfo['str'] = true
					}
					dayInfo['signInBean'] = signInBean + increase * i
					cycleList.push(dayInfo)
				}
				that.signInInfo = {
					list: cycleList,
					continuity: data.continuity,
					today: data.today
				}
			})
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
	  var that = this;
	  that.page = 1;
	  that._onLoad(() => {
	    uni.stopPullDownRefresh();
	  });
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.getListData();
	// },
	// 分享
	onShareAppMessage() {
		let shareData = {
			title: '',
			path: `pages/index/index?${this.userInfo.id}`,
			imageUrl: ''
		}
		return signIn.onShareAppMessage(shareData);
	}
}
